//,,
const state = {
	count : 100

};
// Vuex  store  mutationVuex  mutation  mutation   (type)    (handler) state 
//commit,,,, store.commit  mutation  payload
const mutations = {
	increment(state){
		state.count ++;
		console.log(this);
		
	}
};
const actions = {
	increment (context ){
		context.commit('increment')
	}
};

export default {
	state,
	mutations,
	actions
	
}



